import * as React from 'react'
import styled from '../styles/styled-components'
// import { transparentize } from 'polished'
import Link from 'gatsby-link'

import { onEvent } from '../styles/mixins'
import { Container } from './Container'

const StyledHeader = styled.header`
  height: 136px;
  color: ${p => p.theme.colors.white};
`

const HeaderInner = styled(Container)`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  padding: ${p => p.theme.dimensions.containerPadding}rem;
`

const HeaderLink = styled(Link)`
  color: ${p => p.theme.colors.white};

  ${onEvent`
    text-decoration: none;
  `};
`

const Brand = styled(HeaderLink)`
  line-height: 1.2;
  flex-grow: 1;
`

const Title = styled.div`
  font-weight: 600;
  font-size: 1.75rem;
`

const SubTitle = styled.div`
  font-weight: 500;
  font-size: 1.5rem;
`

const Nav = styled.nav`
  display: flex;
  flex-flow: row nowrap;
  font-size: 1.125rem;
  font-weight: 400;
`

const NavLink = styled(HeaderLink).attrs({
  activeClassName: 'active',
  exact: true,
})`
  margin-right: 2rem;
  text-transform: uppercase;

  &:last-child {
    margin-right: 0;
  }

  &.active {
    font-weight: 600;
  }
`

interface HeaderProps {
  title: string
}

export const Header: React.SFC<HeaderProps> = () => (
  <StyledHeader>
    <HeaderInner>
      <Brand to="/" href="/">
        <Title>Makeomatic</Title>
        <SubTitle>innovation lab</SubTitle>
      </Brand>
      <Nav>
        <NavLink to="/" href="/">
          Work
        </NavLink>
        <NavLink to="/team" href="/team">
          About
        </NavLink>
        <NavLink to="/process" href="/process">
          Process
        </NavLink>
        <NavLink to="/contacts" href="/contacts">
          Contacts
        </NavLink>
      </Nav>
    </HeaderInner>
  </StyledHeader>
)



