import * as React from 'react'
import styled from '../styles/styled-components'
import { Container } from './Container'
import GatsbyLink from 'gatsby-link'

const StyledContactsContainer = styled.section`
  background-color: ${p => p.theme.colors.white};
  color: ${p => p.theme.colors.black};
`

const StyledContacts = styled(Container)`
  padding: ${p => p.theme.dimensions.containerPadding * 1.5}rem ${p => p.theme.dimensions.containerPadding}rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.125rem;
  font-weight: 500;

  a {
    color: ${p => p.theme.colors.black};
  }
`

const Brand = styled.div`
  font-weight: 700;
  font-size: 1.75rem;
  line-height: 1.15;
`

const Address = styled.address`
  font-style: normal;

  p:last-child {
    margin-bottom: 0;
  }
`

const Links = styled.div`
`

const StyledLink = styled(GatsbyLink)`
  display: block;
  text-transform: uppercase;
`

const Social = styled.div`
`

const Github = styled.div`
`

const Behance = styled.div`
`

export const Contacts: React.SFC<{}> = () => (
  <StyledContactsContainer>
    <StyledContacts>
      <Brand>
        Makeomatic
        <br />
        innovation
        <br />
        lab
      </Brand>
      <Address>
        <p>
          +1 (236) 777 41 46
          <br />
          info@makeomatic.ca
        </p>
        <p>
          602 Citadel Parade,
          <br />
          Vancouver, BC Canada
        </p>
      </Address>
      <Links>
        <StyledLink to="/">
          Work
        </StyledLink>
        <StyledLink to="/team">
          About
        </StyledLink>
        <StyledLink to="/process">
          Process
        </StyledLink>
        <StyledLink to="/contacts">
          Contacts
        </StyledLink>
      </Links>
      <Social>
        <Github />
        <Behance />
      </Social>
    </StyledContacts>
  </StyledContactsContainer>
)



